import React, { Component } from 'react';
import "./../css/layout.css"
import "./../css/default.css"
import "./../css/fonts.css"
import "./../css/magnific-popup.css"
import "./../css/media-queries.css"
import lakeImage from "./../assets/lakeImage.jpg"

class Portfolio extends Component {
    render() {
        if (this.props.data !== undefined) {
            console.log(this.props.data);
            var projectsFormatted = this.props.data.mainprojects.map(function (project) {
                var projectImage = lakeImage;
                return (
                    <div className="item">
                        <a href={project.url}>
                            <img src={projectImage} alt="Project" className="image" />
                        </a>
                        <p className="description">
                            <b>{project.title + " "}</b>
                            {project.description}
                        </p>

                    </div>
                );
            })

            var miniFormatted = this.props.data.miniprojects.forEach(function (project, index) {
                if (index % 2) {
                    var projectImage = lakeImage;

                    return (
                        <div className="wrapper">
                            <div className="item">
                                <a href={project.url}>
                                    <img src={projectImage} alt="Project" className="image" />
                                </a>
                                <p className="description">
                                    <b>{project.title + " "}</b>
                                    {project.description}
                                </p>
                            </div>
                        </div>
    
                    );
                }
            })
        }
        return (
            <section id="projects">
                <div className="row portfolio">
                    <div className="four columns header-col" style={{ width: "100%", marginBottom: "10px" }}>
                        <h1><span>Some of my Projects</span></h1>
                    </div>
                    <div className="twelve columns main-col">
                        <div className="wrapper">
                            {projectsFormatted}
                        </div>
                        <div className="wrapper">
                            {projectsFormatted}
                        </div>
                    </div>
                </div>

                <div className="row portfolio">
                    <div className="four columns header-col" style={{ width: "100%", marginBottom: "10px" }}>
                        <h1><span>Miscellaneous Projects</span></h1>
                    </div>
                    <div className="twelve columns main-col">
                        {miniFormatted}
                    </div>
                </div>
            </section>
        );
    }
}

export default Portfolio;
